import {LitElement, html, customElement, property} from 'lit-element';
import { environment } from '../../environment/environment';
import { User } from '../../lib/models/User/User';

//css styles
// @ts-ignore
import mainCSS from '../../assets/css/main.css';
// @ts-ignore
import normalizeCSS from '../../assets/css/normalize.css';

@customElement('deposit-elem')
class DepositElement extends LitElement {
  
  constructor() {
    super();
    this.stablyDepositUrl = environment.STABLY_DEPOSIT_URL;
  }

  static styles = [
    mainCSS,
    normalizeCSS
  ];

  @property() stablyDepositUrl: string;
  @property() user: User | undefined;

  private backToHomeViewEvent(){
    let event = new CustomEvent('backToHomeView', {});
    this.dispatchEvent(event);
  }

  protected render(): unknown {
    return html`
      <div class="widget" id="view-address">
        <!-- Navigation -->
        <div class="grid">
          <div class="grid-cell">
            <a @click=${this.backToHomeViewEvent} class="back">Back</a>
          </div>
          <div class="grid-cell">
            <p class="label-upper text-center">Deposit</p>
          </div>
          <div class="grid-cell">

          </div>
        </div>
        <div class="grid grid-center margin-top-25">
          <div class="grid-cell">
            <p class="text-center">Buy Stably USD and send to Bridge</p>
            <p class="text-center margin-bottom-15 label-small">Requires a verified identity.</p>
            <p class="text-left label-small margin-bottom-15">Stably USD (USDS) is a cryptocurrency tied to 1&nbsp;USD. To buy USDS and add it to Bridge:</p>
            <ol>
              <li class="label-small">Sign in to Stably and <a href="https://prime.stably.io/accounts/usd/deposit" rel="noreferrer noopener" target="_blank">deposit USD</a> via wire transfer or a US bank account.</li>
              <li class="label-small">Click 'Go to Stably' to open the Stably withdraw page, with your Bridge address filled in.</li>
              <li class="label-small">Choose the amount to send, then confirm the transaction.</li>
            </ol>
            <p class="label-small">You'll receive USDS in Bridge after 10+ minutes.</p>
          </div>
        </div>
        <div class="grid">
          <div class="grid-cell text-center">
            <p class="margin-top-15 margin-bottom-15">
              <a href=${`${this.stablyDepositUrl}&address=${this.user?.iconWalletAddress}`} class="button" rel="noreferrer noopener" target="_blank">
                Go to Stably
              </a>
            </p>
          </div>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'deposit-elem': DepositElement;
  }
}