import {LitElement, html, customElement, property} from 'lit-element';
import { environment } from '../../environment/environment';
import { User } from '../../lib/models/User/User';

//css styles
// @ts-ignore
import mainCSS from '../../assets/css/main.css';
// @ts-ignore
import normalizeCSS from '../../assets/css/normalize.css';

@customElement('withdraw-elem')
class WithdrawElement extends LitElement {
  
  constructor() {
    super();
    this.stablyWithdrawUrl = environment.STABLY_WITHDRAW_URL;
  }

  static styles = [
    mainCSS,
    normalizeCSS
  ];

  @property() stablyWithdrawUrl: string;
  @property() user: User | undefined;

  private backToHomeViewEvent(){
    let event = new CustomEvent('onMenuTriggerClick', {});
    this.dispatchEvent(event);
    event = new CustomEvent('backToHomeView', {});
    this.dispatchEvent(event);
  }

  protected render(): unknown {
    return html`
      <div class="widget" id="view-address">
        <!-- Navigation -->
        <div class="grid">
          <div class="grid-cell">
            <a @click=${this.backToHomeViewEvent} class="back">Back</a>
          </div>
          <div class="grid-cell">
            <p class="label-upper text-center">Withdraw</p>
          </div>
          <div class="grid-cell">

          </div>
        </div>
        <div class="grid grid-center margin-top-25">
          <div class="grid-cell">
          <p class="text-center">Send Stably USD to your bank account</p>
          <p class="text-center margin-bottom-15 label-small">Requires a verified identity.</p>
          <p class="text-left label-small margin-bottom-15">Send USDS to your Stably account, then withdraw it to your bank:</p>
          <ol>
            <li class="label-small">Click 'Go to Stably', sign in, and copy the ICON address provided.</li>
            <li class="label-small">Use Bridge to send at least 10 USDS to that address (takes 10+ minutes).</li>
            <li class="label-small">Go to the <a href="https://prime.stably.io/accounts/usd/withdraw" rel="noreferrer noopener" target="_blank">Stably USD withdraw page</a>, add your bank details, then complete the transaction.</li>
          </ol>
          </div>
        </div>
        <div class="grid">
          <div class="grid-cell text-center">
            <p class="margin-top-15 margin-bottom-15">
              <a href=${this.stablyWithdrawUrl} class="button" rel="noreferrer noopener" target="_blank">
                Go to Stably
              </a>
            </p>
          </div>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'withdraw-elem': WithdrawElement;
  }
}